
<template>
    <!-- <div id="box">
        <h1>账号登录</h1>
        <div id="box-one">
            <input type="text" placeholder="用户名">
            <input type="text" placeholder="用户名">

        </div>
    </div> -->
    <!-- Login -->
  <div class="login">
    <div class="login-form">
      <h3>登陆界面</h3>
      <input type="text" placeholder="请输入用户名" id="name" autocapitalize="off" v-model.trim=name aria-autocomplete="off">
      <input type="password" placeholder="请输入密码" id="password" autocapitalize="off" v-model.trim="password">
      <div class="login-button">
        <button type="primary" v-on:click="inputInfo">登录</button>
        <button type="info" v-on:click="resetInfo">重置</button>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript" src="../js/flexible.js"></script>

<style>
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }
    .login {
        width: 100%;
        height: 13.41rem;
        overflow: hidden;
        position: relative;
        /* border: 0.1rem solid red; */
        background-color: #C0C1C5;
        /* background-image: url("../assets/bgcolor.jpg");
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-position: center;
        background-size: cover; */
    }
    .login-form {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 8rem;
        /* height: 5rem; */
        min-width: 3rem;
        max-width: 8rem;
        display: flex;
        flex-direction: column;
        background-color: rgba(255, 255, 255, 0.7);
        border-radius: 0.2rem;
        box-shadow: 0 0.2rem 0.3rem rgba(255, 255, 255, .5);
    }
    .login-form h3 {
        /* width: 60%; */
        margin: 0.2rem auto 0;
        text-align: center;
    }
    .login-form input {
        width: 60%;
        margin: 0.3rem auto;
        outline: none;
        padding: 0.15rem;
        border: 0.02rem solid #8B8C91;
        border-radius: 0.1rem;
        background: transparent;
        color: black;
    }
    .login ,.login-form, .login-button{
        width: 100%;
        /* margin: 10px auto; */
        display: flex;
        justify-content: center;
        align-content: center;
    }
    /* p {
        width: 60%;
        margin: 8px auto;
        position: relative;
        left: -15px;
        color: #ff0000;
        font-size: 8px;
    } */
    .login-form button {
        background-color: #007bff;
        color: white;
        margin: 0.1rem;
        padding: 0.1rem 0.2rem;
        border: none;
        font-size: 0.3rem;
        border-radius: 0.1rem;
        cursor: pointer;
    }
</style>

<script>
// import axios from 'axios'
export default {
    name: "Login",
    data: function () {
    return {
        name: '',
        password: '',
        }
    },
    methods: {
        // 清空当前填写信息
        resetInfo: function () {
        this.name = "";
        this.password = "";
        },
        // 验证信息是否正确
        inputInfo: function () {
        if (this.name !=="" && this.name !== "admin") {
            this.password = "";
            return 'err';
        }
        if (this.password !=="" && this.password !== "123") {
            this.password = "";
            return 'err';
        }
        if (this.name == "" || this.password == "") {
            alert("请输入用户名和密码")
            return 'err';
        }
        // 返回上面 , 提交信息
        // this.requestInfo();
        // 跳转到 /home
        this.$router.push("/UploadFile");
        }
    },
    computed: {

    },
    created(){
        (function flexible(window, document) {
            var docEl = document.documentElement;
            var dpr = window.devicePixelRatio || 1;

            // adjust body font size
            function setBodyFontSize() {
                if (document.body) {
                document.body.style.fontSize = 12 * dpr + "px";
                } else {
                document.addEventListener("DOMContentLoaded", setBodyFontSize);
                }
            }
            setBodyFontSize();

            // set 1rem = viewWidth / 10
            function setRemUnit() {
                var rem = docEl.clientWidth / 24;
                docEl.style.fontSize = rem + "px";
            }

            setRemUnit();

            // reset rem unit on page resize
            window.addEventListener("resize", setRemUnit);
            window.addEventListener("pageshow", function(e) {
                if (e.persisted) {
                setRemUnit();
                }
            });

            // detect 0.5px supports
            if (dpr >= 2) {
                var fakeBody = document.createElement("body");
                var testElement = document.createElement("div");
                testElement.style.border = ".5px solid transparent";
                fakeBody.appendChild(testElement);
                docEl.appendChild(fakeBody);
                if (testElement.offsetHeight === 1) {
                docEl.classList.add("hairlines");
                }
                docEl.removeChild(fakeBody);
            }
        })(window, document);
    }
}
</script>



