<template>
    <div class="box">
        <!-- 文件上传页面 -->
        <div class="fileLoad" v-show="isShowPage">
            <div>
                <label>上传列表</label>
                <!-- <label @click="HidePage" style="color: #007BFF;margin-left: 10.68rem;">关闭页面</label> -->
                <label @click="HidePage" style="color: #007bff;float: right;">关闭页面</label>
            </div>
            <div>
                <div style="color: #EA7725;">正在上传</div>
                <div class="upload" :key="index" v-for="(item, index) in fileList2">
                    <div>{{ item.name }}</div>
                    <div style="color: #007BFF;">{{ item.progress }}%</div>
                </div>

            </div>
        </div>
        <!-- 新建文件夹弹出页面 -->
        <div class="filePopup" v-show="isShowPop">
            <div>
                <label>新建文件夹</label>
            </div>
            <div>
                <input type="text" placeholder="请输入文件夹名" v-model="folder"><br>
                <button @click="CreateFolder">提交</button>
                <button @click="HidePop">取消</button>
            </div>
        </div>

        <div class="box-form">
            <input style="display:none;" type="file" ref="fileInput" multiple @change="handleFileChange">
            <button type="primary" @click="uploadFiles"><img src="../img/上传.png">上传</button>
            <button type="primary" @click="ShowPop"><img src="../img/文件夹.png">新建文件夹</button>
            <button type="primary" @click="Download"><img src="../img/下载.png">下载</button>
            <button type="primary" @click="Delete"><img src="../img/删除.png">删除</button>
        </div>
        <div style="font-size: 0.25rem;margin: 0.1rem;">
            <label style="color:#007BFF" @click="GetAllFileList">全部文件</label>
            <label :key="index" v-for="(item,index) in prefixList">/
                <span style="color:#007BFF" @click="FolderJump(index)">{{item}}</span>
            </label>
        </div>
        <div>
            <table class="mytable" border="0">
                <tr>
                    <td><input type="checkbox" @change="allChecked" v-model="isChecked" class="allChecked"></td>
                    <td>文件名</td>
                    <td>修改时间</td>
                    <td>大小</td>
                </tr>
                <tr :key="index" v-for="(item, index) in fileList">
                    <td><input type="checkbox" class="checked" v-model="item.checked" @change="Checked"></td>
                    <td @click="OpenFolder(item.key,item.type)">
                        <a href="#" v-if="item.type=='folder'">{{ item.fileName }}</a>
                        <span v-else>{{ item.fileName }}</span>
                    </td>
                    <td>{{ item.createTime }}</td>
                    <td>{{ item.fileSize }}</td>
                </tr>
            </table>
        </div>

    </div>
</template>
<script type="text/javascript" src="../js/flexible.js"></script>
<script>
import axios from 'axios'
export default {
    data: function () {
        return {
            isChecked: false,
            isShowPage:false,
            isShowPop:false,
            fileList:[
                // { fileName:'文件1',fileSize:'1KB',createTime:'2023/10/15 18:33',checked:false },
                // { name:'文件2',size:'2KB',createTime:'2023/10/15 18:33',checked:false },
                // { name:'文件2',size:'3KB',createTime:'2023/10/15 18:33',checked:false }
            ],
            fileList2:[
                // {name:'文件1',progress:'20'},
                // {name:'文件2',progress:'30'},
                // {name:'文件3',progress:'40'}
            ],
            files:[],
            prefix:"",
            prefixList:[],
            folder:"",
        }
    },
    created(){
        this.GetFileList();

        (function flexible(window, document) {
            var docEl = document.documentElement;
            var dpr = window.devicePixelRatio || 1;

            // adjust body font size
            function setBodyFontSize() {
                if (document.body) {
                document.body.style.fontSize = 12 * dpr + "px";
                } else {
                document.addEventListener("DOMContentLoaded", setBodyFontSize);
                }
            }
            setBodyFontSize();

            // set 1rem = viewWidth / 10
            function setRemUnit() {
                var rem = docEl.clientWidth / 24;
                docEl.style.fontSize = rem + "px";
            }

            setRemUnit();

            // reset rem unit on page resize
            window.addEventListener("resize", setRemUnit);
            window.addEventListener("pageshow", function(e) {
                if (e.persisted) {
                setRemUnit();
                }
            });

            // detect 0.5px supports
            if (dpr >= 2) {
                var fakeBody = document.createElement("body");
                var testElement = document.createElement("div");
                testElement.style.border = ".5px solid transparent";
                fakeBody.appendChild(testElement);
                docEl.appendChild(fakeBody);
                if (testElement.offsetHeight === 1) {
                docEl.classList.add("hairlines");
                }
                docEl.removeChild(fakeBody);
            }
        })(window, document);
    },
    methods:{
        allChecked(){
            var isAll = this.isChecked;
            this.fileList.forEach(function(element) {
                element.checked = isAll
            });
        },
        Checked(){
            this.isChecked = this.fileList.every( item => item.checked );
        },
        ShowPage(){
            this.isShowPage = true;
        },
        HidePage(){
            this.isShowPage = false;
            this.fileList2 = [];
        },
        ShowPop(){
            this.isShowPop = true;
        },
        HidePop(){
            this.isShowPop = false;
        },
        handleFileChange(){
            const that = this;
            that.files = Array.from(this.$refs.fileInput.files);
            const selectedFile = event.target.files[0]; // 在这里执行你需要的逻辑，例如上传文件或其他处理
             if (selectedFile) {
                that.isShowPage = true;
                const formData = new FormData();
                that.files.forEach(file => {
                    var obj = { name:file.name,progress:"0" };
                    that.fileList2.push(obj);
                    formData.append("files", file);
                });
                var uploadFiles = that.fileList2;
                var url = "https://localhost:44330/api/UploadFile/BatchUploadV2?folder="+this.prefix;
                fetch(url,{
                    method: 'POST', // 或 'GET'，根据您的需求
                    body: formData, // 将FormData作为请求体
                })
                .then(response => {
                    if (response.ok) {
                        const reader = response.body.getReader();
                        const decoder = new TextDecoder('utf-8');

                        //处理流数据
                        return reader.read().then(function processText({ done, value }) {
                            if (done) {
                                that.GetFileList();
                                console.log('Stream complete');
                                return;
                            }

                            const text = decoder.decode(value);
                            const json = JSON.parse(text);
                            that.fileList2.forEach(function(item) {
                                if(item.name==json.fileName){
                                    item.progress=json.progress;
                                }
                            });

                            // 继续处理下一块数据
                            return reader.read().then(processText);
                        });
                        console.log(response)
                    } else {
                        console.error('Request failed');
                    }
                })
                .catch(error => console.error('Fetch error:', error));
                }
        },
        uploadFiles(){
            this.$refs.fileInput.click();
        },
        GetAllFileList(){
            this.prefix="";
            this.prefixList = [];
            this.GetFileList();
        },
        GetFileList(){
            var url = "https://localhost:44330/api/File/GetFileList";
            axios.get(url,{
            params: {
                prefix:this.prefix
            }})
            .then(res=>{
                console.log(this.prefix)
                console.log(res.data.fileList)
                this.fileList=res.data.fileList;
                this.fileList.forEach(element => {
                    element.key = element.fileName;
                    element.fileName = element.fileName.replace(this.prefix,"");
                });
            },err=>{
                console.log(err);
            })
        },
        OpenFolder(fileName,type){
            if(type!="folder"){
                return;
            }
           this.prefix = fileName;
           this.prefixList = fileName.split('/')
           this.GetFileList()
        },
        FolderJump(index){
            this.prefix = "";
            for (let i = 0; i <= index; i++) {
                this.prefix =this.prefix + this.prefixList[i] + "/";
            }
            this.prefixList = this.prefix.split('/')
            this.GetFileList();
        },
        CreateFolder(){
            var folder = this.prefix + this.folder + "/";
            console.log(folder);
            var url = "https://localhost:44330/api/Folder/Create";
            axios.get(url,{
            params: {
                folder:folder
            }})
            .then(res=>{
                console.log(res);
                this.GetFileList();
            },err=>{
                console.log(err);
            })
            this.folder = "";
            this.isShowPop=false;
        },
        Download(){
            var fileList = this.fileList.filter(item=>{
                if(item.checked){
                    return item.fileName;
                }
            })
            var fileName = fileList.map(item=>{
                return this.prefix + item.fileName
            })
            var url = "https://localhost:44330/api/Download/BatchDownload";
            axios.get(url,{
            params: {
                fileName:fileName.join(',')
            }})
            .then(res=>{
                for (let i = 0; i < res.data.urlList.length; i++) {
                    const element = res.data.urlList[i];
                    fetch("http://"+element)
                    .then(response => response.blob())
                    .then(blob => {
                        const link = document.createElement('a');
                        link.href = window.URL.createObjectURL(blob);
                        link.download = fileName[i];
                        link.click();
                    }).catch(error => {
                        console.error('下载文件时发生错误：', error);
                    });
                }
            },err=>{
                console.log(err);
            })
        },
        Delete(){
            var url = "https://localhost:44330/api/Delete/BatchDelete";
            var fileList = this.fileList.filter(item=>{
                if(item.checked){
                    return item.fileName;
                }
            })
            var fileName = fileList.map(item=>{
                return this.prefix + item.fileName
            })
            console.log(fileName)
            var check = fileList.map(item=>{
                return this.prefix + item.checked
            })
            var i=''
            for (let index = 0; index < check.length; index++) {
                i = check[index];
            }
            if (i=='true') {
                let del=confirm("请确认是否删除");
                if (del==true) {
                    axios.get(url,{
                    params: {
                        fileName:fileName.join(',')
                    }})
                    .then(res=>{
                        console.log(res);
                        this.GetFileList();
                    },err=>{
                        console.log(err);
                    })
                }
            }
            else{
                alert(请勾选要删除的文件)
            }
        },
    }
}
</script>

<style>
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.box {
    /* border: 1px red solid; */
    /* overflow: hidden; */
    position: relative;
    /* border: 0.1rem solid red;*/
}

.box-form {
    display: flex;
}

.box-form img {
    width: 0.3rem;
    height: 0.3rem;
    margin-left: 0.02rem;
}

.box-form button {
    background-color: #007bff;
    color: white;
    margin: 0.1rem;
    padding: 0.1rem 0.3rem;
    border: none;
    font-size: 0.25rem;
    border-radius: 0.3rem;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.box-form button:fir {
    background-color: red;
}

.mytable {
    border-collapse: collapse;
    width: 96%;
    margin: 0.3rem auto;
    font-size: 0.23rem;
}

.mytable tr {
    border-bottom: 1px #BBBFC1 solid;
}

.mytable tr td {
    padding: 0.25rem;
}

.fileLoad {
    position: fixed;
    top: 20%;
    left: 25%;
    width: 13rem;
    height: 8rem;
    background-color: rgba(255, 255, 255, 1);
    border-radius: 0.2rem;
    box-shadow: 0 0.2rem 0.3rem rgba(0, 0, 0, .2);
    font-size: 0.25rem;
}

.fileLoad>div:first-child {
    margin: 0.1rem;
    border-bottom: 0.02rem solid #D8DBDD;
    padding-bottom: 0.1rem;
}

.fileLoad>div:last-child {
    margin: 0.1rem;

}

.upload{
    margin-top: 0.1rem;
}
.filePopup {
    position: fixed;
    top: 20%;
    left: 40%;
    width: 5rem;
    height: 4rem;
    background-color: rgba(255, 255, 255, 1);
    border-radius: 0.2rem;
    box-shadow: 0 0.2rem 0.3rem rgba(0, 0, 0, .2);
    font-size: 0.25rem;
}

.filePopup>div:first-child {
    margin: 0.1rem;
    border-bottom: 0.02rem solid #D8DBDD;
    padding-bottom: 0.1rem;
}

.filePopup>div:last-child {
    margin: 0.1rem;

}

.filePopup input{
    width: 100%;
    margin: 0.3rem auto;
    outline: none;
    padding: 0.1rem;
    border: 0.02rem solid #8B8C91;
    border-radius: 0.1rem;
    background: transparent;
    color: black;
}
.filePopup button{
    background-color: #007bff;
    color: white;
    /* margin: 0.1rem; */
    padding: 0.1rem 0.2rem;
    border: none;
    font-size: 0.25rem;
    border-radius: 0.1rem;
    cursor: pointer;
}

.filePopup，.filePopup >div{
    width: 100%;
    /* margin: 10px auto; */
    display: flex;
    justify-content: center;
    align-content: center;
}

</style>



