import Vue from 'vue'
import Router from 'vue-router'
import Login from '../view/Login'
import UploadFile from '../view/UploadFile'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/UploadFile',
      name: 'UploadFile',
      component: UploadFile
    }
  ]
})



